import React, {useState, useEffect, fetchData} from 'react';
import PropTypes from 'prop-types';
import bridge from '@vkontakte/vk-bridge';

import { Panel, PanelHeader, Avatar, SimpleCell, Group, PanelHeaderBack, Search, Cell, Footer, SplitLayout, SplitCol, ScreenSpinner} from '@vkontakte/vkui';



const ChangeGroup = props => {

    const [search, setSearch] = useState('');
    const [groups, setGroups] = useState([]);
    const [popout, setPopout] = useState(<ScreenSpinner state="loading" />);

    const fetchData = async () => {
        await fetch('https://edu.donstu.ru/api/raspGrouplist?year=2022-2023')
        .then(response => response.json())
        .then((data) => {
            setGroups(data.data)
            setPopout(null)
        });
    };
      useEffect(() => {
        fetchData();
      }, []);

    const onChange = (e) => {
        setSearch(e.target.value);
    };

    const thematicsFiltered = groups.filter(
        ({ name }) => name.toLowerCase().indexOf(search.toLowerCase()) > -1,
    );
     
    const choise = async (group) => {
        props.setGroup({'idGroup': group.id,'group_name': group.name})
        props.go('UserSettings')
            // const hash = await bridge.send("VKWebAppCreateHash")
            // await fetch('http://127.0.0.1:5000/api/setGroup?user_id='+props.fetchedUser.id+'&group_id='+group.id+'&group_name='+group.name+'&token='+hash.sign+'&ts='+hash.ts)
            // .then(response => response.json())
            // .then((data) => {
                
            // });
        
    }
    return (<Panel id={props.id}>
    <PanelHeader before={<PanelHeaderBack onClick={props.go} data-to="UserSettings"/>}>Изменение группы</PanelHeader>
        <SimpleCell before={<Avatar size={48} src={props.fetchedUser.photo_200}></Avatar>} subtitle={props.group.group_name}>
            {props.fetchedUser.first_name} {props.fetchedUser.last_name}
        </SimpleCell>
    <Group>
        <Search value={search} onChange={onChange} after={null} />
        <SplitLayout popout={popout}>
        <SplitCol>
        {thematicsFiltered.length > 0 && search.length > 1 &&
          thematicsFiltered.map((group) => <Cell onClick={() => {choise(group)}} key={group.id}>{group.name} </Cell>)}
        {thematicsFiltered.length === 0 && <Footer>Ничего не найдено</Footer>}
        </SplitCol>
        </SplitLayout>
    </Group>
    
    </Panel> 
    )
}

export default ChangeGroup;