import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import { View, ScreenSpinner, AdaptivityProvider, AppRoot, ConfigProvider, SplitLayout, SplitCol, Panel } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Rasp from './panels/Rasp';
import UserSettings from './panels/UserSettings';
import ChangeGroup from './panels/ChangeGroup';

const App = () => {

  const [activePanel, setActivePanel] = useState('home');
  const [fetchedUser, setUser] = useState(null);
  const [group, setGroup] = useState({})
  const [popout, setPopout] = useState(<ScreenSpinner size='large'/>);

  const fetchData = async () => {
    const user = await bridge.send("VKWebAppGetUserInfo")
    const hash = await bridge.send("VKWebAppCreateHash")
    setUser(user)
    // await fetch('http://127.0.0.1:5000/api/getUser?user_id='+user.id+'&token='+hash.sign+'&ts='+hash.ts)
    //     .then(response => response.json())
    //     .then((data) => {
    //       setGroup(data)
    //     });
    setPopout(null);
  }

  useEffect(() => {
    fetchData();
  }, []);

  const go = e => {
    if (typeof e === 'string'){
      setActivePanel(e);
    }else{
      setActivePanel(e.currentTarget.dataset.to);
    }
    
  };
  return (
    <ConfigProvider>
      <AdaptivityProvider>
        <AppRoot>
          <SplitLayout popout={popout}>
            <SplitCol>
              <View activePanel={activePanel}>
                <Home id='home' go={go} fetchedUser={fetchedUser} group={group}/>
                <Rasp id='rasp' go={go}/>
                <UserSettings id='UserSettings' go={go} fetchedUser={fetchedUser} group={group}/>
				        <ChangeGroup id='ChangeGroup' go={go} fetchedUser={fetchedUser} group={group} setGroup={setGroup}/>
              </View>
            </SplitCol>
          </SplitLayout>
        </AppRoot>
      </AdaptivityProvider>
    </ConfigProvider>
  );
}

export default App;