import React, {useState, useEffect, fetchData} from 'react';
import PropTypes from 'prop-types';

import { Panel, PanelHeader, Avatar, SimpleCell, Group, PanelHeaderBack, CellButton} from '@vkontakte/vkui';

import { Icon24GearOutline } from '@vkontakte/icons';

const UserSettings = props => {

    return (<Panel id={props.id}>
    <PanelHeader before={<PanelHeaderBack onClick={props.go} data-to="home"/>}>О себе</PanelHeader>
        <SimpleCell before={<Avatar size={48} src={props.fetchedUser.photo_200}></Avatar>} subtitle={props.group.group_name}>
            {props.fetchedUser.first_name} {props.fetchedUser.last_name}
        </SimpleCell>
    <Group>
        <CellButton before={<Icon24GearOutline />} onClick={props.go} data-to='ChangeGroup'>Изменить группу</CellButton>
    </Group>
    </Panel> 
    )
}

export default UserSettings;