import React, {useState, useEffect} from 'react';
import PropTypes from 'prop-types';

import { Panel, PanelHeader, Card, PanelHeaderButton, Group, CardGrid, InfoRow, MiniInfoCell } from '@vkontakte/vkui';

import { Icon24User, Icon20CommunityName } from '@vkontakte/icons';

function weekrender (week, rasps) {
    return (
		<Card>
		<MiniInfoCell textWrap="full" textLevel="primary">{week}</MiniInfoCell>
		{rasps.map((para) => {
        if (para.день_недели == week) {
		return(
		<div style={{ }}>
		
		<MiniInfoCell textWrap="full" textLevel="primary">{para.номерЗанятия} пара</MiniInfoCell>
        <MiniInfoCell textWrap="full" textLevel="primary">Дисциплина: {para.дисциплина}</MiniInfoCell>
        <MiniInfoCell textWrap="full" textLevel="primary">Преподаватель: {para.преподаватель}</MiniInfoCell>
		</div>)
		}})}
		</Card>
    )
}

const Home = props => {
	const [rasps, setRasps] = useState([]);

    useEffect(() => {
        async function getRasp(){
            await fetch(`https://edu.donstu.ru/api/Rasp?idGroup=${props.group.idGroup}&sdate=2023-04-16`)
            .then(response => response.json())
            .then((data) => {
                setRasps(data.data.rasp)
            });
        };
        getRasp();
    },[]);
	return (<Panel id={props.id}>
		<PanelHeader before={
		<PanelHeaderButton onClick={props.go} data-to='UserSettings'><Icon24User/></PanelHeaderButton>}>
			Главная
		</PanelHeader>
		<Group description="Внутри Group">
		<CardGrid size="l">
			
			{weekrender('Понедельник',rasps)}
			{weekrender('Вторник',rasps)}
			{weekrender('Среда',rasps)}
			{weekrender('Четверг',rasps)}
			{weekrender('Пятница',rasps)}
			{weekrender('Суббота',rasps)}
		</CardGrid>
		</Group>
	</Panel>)
};

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Home;
