import React, {useState, useEffect} from 'react';
import PropTypes from 'prop-types';

import { Panel, PanelHeader, MiniInfoCell, Group, Button, ButtonGroup, Header, HorizontalScroll} from '@vkontakte/vkui';
//'http://127.0.0.1/api/get-user?from_id=1'
//https://edu.donstu.ru/api/Rasp?idGroup=44434&sdate=2023-04-16


function weekrender (week, rasps) {
    
    return (
    <Group header={<Header mode="secondary">{week}</Header>} padding='s'>
          <HorizontalScroll
            showArrows
            getScrollToLeft={(i) => i - 120}
            getScrollToRight={(i) => i + 120}
          >
            <div style={{ display: 'flex', marginLeft: 2} }>
            {rasps.map((para) => {
            if (para.день_недели == week) {
                return (
                <Group>
                    <MiniInfoCell textWrap="full" textLevel="primary">{para.номерЗанятия} пара</MiniInfoCell>
                    <MiniInfoCell textWrap="full" textLevel="primary">Дисциплина: {para.дисциплина}</MiniInfoCell>
                    <MiniInfoCell textWrap="full" textLevel="primary">Преподаватель: {para.преподаватель}</MiniInfoCell>
                </Group>      
                )
                }})
            }                                                                                 
            </div>
          </HorizontalScroll>
        </Group>
    )
}

const Rasp = ({ id, go }) => {
    const [rasps, setRasps] = useState([]);

    useEffect(() => {
        async function getRasp(){
            await fetch('https://edu.donstu.ru/api/Rasp?idGroup=44434&sdate=2023-04-16')
            .then(response => response.json())
            .then((data) => {
                setRasps(data.data.rasp)
            });
        };
        getRasp();
    },[]);
    
    console.log(rasps)
    return (<Panel id={id}>
        <PanelHeader>Расписание на неделю</PanelHeader>

        {weekrender('Понедельник',rasps)}
        {weekrender('Вторник',rasps)}
        {weekrender('Среда',rasps)}
        {weekrender('Четверг',rasps)}
        {weekrender('Пятница',rasps)}
        {weekrender('Суббота',rasps)}
        
        
        
        </Panel>)
        
        
};

Rasp.propTypes = {
	id: PropTypes.string.isRequired,

};

export default Rasp;